  padding: 0;
  width: 100vw;
  height: 100vh;
  font-size: 1.5rem;
  font-family: "Operator Mono", "Fira Code", monaco, sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App > div {
  display: -ms-flexbox;
  display: flex;
}

.App-option {
  width: 100%;
  white-space: nowrap;
  word-break: keep-all;
}
.App-option:hover {
  background: #f5f5f5 !important;
  color: black !important;
}

.bottom {
  bottom: 50px;
  position: absolute;
}



